.dropdown-wrapper{
   height: 100%;
   width: 100%;
   background-color: rgba(0, 0, 0, 0.199);

   .dropdown-container{
         background-color: white;
         -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
         -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
         box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
         // box-sizing: border-box;
         position: absolute;
         right: calc(100% - 200%);
         top: 40px;
         z-index: 999;
         min-width: 250px;
         min-height: 200px;
         border-radius: 10px;
         
         .dropdown-body{
            padding: 20px 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 10px;
            font-size: 14px;
   
            & > .dropdown-item{
               width: 100%;
               min-height: 50px;
               display: flex;
               align-items: center;
               justify-content: center;
               font-weight: 600;
               padding-left: 10px;
               cursor: pointer;

               &:hover{
                  background-color: rgba(128, 128, 128, 0.205);
               }
            }
   
            .warning{
               display: inline-flex;
            }
         }
      }
}
