.preloader-wrapper{
   width: 100%;
   height: 80vh;
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;

   .loader{
      width: 200px;
      height: 200px;
      position: relative;

      .circle1{
         width: 100px;
         height: 100px;
         position: absolute;
         top: calc(50% - 50px);
         left: calc(50% - 50px);
         fill: none;
         stroke: orangered;
         stroke-width: 7px;
         stroke-dasharray: 290;
         stroke-linecap: round;
         stroke-dashoffset: 290;
         animation: circle 2s cubic-bezier(0.19, .2, 0, 1.01) infinite;
      }

      .circle2{
         width: 140px;
         height: 140px;
         position: absolute;
         top: calc(50% - 70px);
         left: calc(50% - 70px);
         fill: none;
         stroke: blueviolet;
         stroke-width: 7px;
         stroke-dasharray: 410;
         stroke-linecap: round;
         stroke-dashoffset: 410;
         animation: circle 2s 0.5s cubic-bezier(0.19, .2, 0, 1.01) infinite;
      }

      .circle3{
         width: 180px;
         height: 180px;
         position: absolute;
         top: calc(50% - 90px);
         left: calc(50% - 90px);
         fill: none;
         stroke: darkblue;
         stroke-width: 7px;
         stroke-dasharray: 535;
         stroke-linecap: round;
         stroke-dashoffset: 535;
         animation: circle 2s 0.25s cubic-bezier(0.19, .2, 0, 1.01) infinite;
      }
   }
}

@keyframes circle {
   100% {
      stroke-dashoffset: 0;
   }
}