
.navbar{
   height: 50px;
   display: flex;
   width: 100%;
   align-items: center;
   justify-content: space-between;
   margin-bottom: 20px;

   .search{
      // display: flex;
      // align-items: center;
      // border: 0.5px solid #555;
      // padding: 0px 5px;
      // margin-left: 20px;

      // input{
      //    border: none;
      //    outline: none;
      //    background-color: transparent;
      // }

      // .icon{
      //    padding: 5px;
      //    cursor: pointer;
      // }
   }

   .items{
      display: flex;
      gap: 20px;
      font-size: 18px;
      margin-right: 20px;
      align-items: center;

      .item{
         margin-right: 10px;
         position: relative;

         .language{
            margin-left: 5px;
         }

         .icon{
            cursor: pointer;
         }

         .notification{
            position: absolute;
            width: 10px;
            height: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            padding: 2px;
            background-color: red;
            font-size: 10px;
            color: white;
            top: -5px;
            right: -10px;
         }

         .avatar{
            width: 35px;
            height: 35px;
            border-radius: 50%;
            border: 1px solid black;
         }
      }
   }
}